header {
  position: fixed;
  width: 100%;
  padding-top: 2.5rem;
  z-index: 5;
}
@media (min-width: 64em) {
  header {
    position: initial;
    padding-top: 3rem;
  }
}
header .logo {
  float: left;
}
header .toggler {
  content: url("../images/icon-hamburger.svg");
  -webkit-appearance: none;
  cursor: pointer;
  padding-top: 0.3125rem;
  float: right;
}
header .toggler:checked {
  content: url("../images/icon-close.svg");
  filter: brightness(0) invert(1);
}
header .toggler:checked ~ .menu__links {
  max-height: 25rem;
  padding-top: 3.125rem;
  pointer-events: all;
}
@media (min-width: 64em) {
  header .toggler:checked ~ .menu__links {
    padding-top: 0;
  }
}
@media (min-width: 64em) {
  header .toggler {
    display: none;
  }
}
header .menu__links {
  list-style: none;
  overflow: hidden;
  clear: both;
  max-height: 0;
  transition: max-height 0.5s ease-out;
  pointer-events: none;
  padding: 0;
  letter-spacing: 2px;
}
@media (min-width: 64em) {
  header .menu__links {
    clear: none;
    float: right;
    overflow: visible;
    margin-top: 0;
    pointer-events: all;
  }
}
header .menu__links li {
  margin-bottom: 1.5rem;
  text-align: center;
  border-bottom: 1px solid var(--gray-blue);
}
