  border-bottom: none;
}
@media (min-width: 64em) {
  header .menu__links li {
    float: left;
    text-align: left;
    border-bottom: none;
    margin: 0;
  }
}
header .menu__links li .menu__link {
  text-decoration: none;
  display: block;
  margin-bottom: 1.625rem;
  font-size: 1.25rem;
  color: white;
}
@media (min-width: 64em) {
  header .menu__links li .menu__link {
    font-size: 0.875rem;
    color: black;
    margin-right: 2.8125rem;
  }
  header .menu__links li .menu__link:hover {
    color: var(--soft-red);
  }
}
header .menu__links li .odd__link {
  text-decoration: none;
  display: block;
  margin-bottom: 1.625rem;
  font-size: 1.25rem;
  color: white;
  border: 3px solid white;
  border-radius: 8px;
  outline: none;
  padding: 1.125rem 1.125rem;
}
@media (min-width: 64em) {
  header .menu__links li .odd__link {
    padding: 0.7rem 2rem 0.7rem 2rem;
    margin-top: -12px;
    font-size: 0.875rem;
    color: white;
    background-color: var(--soft-red);
  }
  header .menu__links li .odd__link:hover {
    background-color: white;
    color: var(--soft-red);
    border: 2px solid var(--soft-red);
  }
}

header.menu-active {
  height: 100vh;
  background-color: var(--overlay);
}

.logo-filter {
