html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  margin: 0;
}

body {
  font-family: var(--font-rubik);
}

.container {
  width: 85%;
  margin: 0 auto;
}

button {
  font-family: var(--font-rubik);
  color: white;
  border: none;
  border-radius: 8px;
}

.spacing > * + * {
  margin-top: var(--spacer, 26.5px);
}

p {
  font-size: clamp(1rem, 0.9rem + 0.38vw, 1.25rem);
  font-weight: 400;
  line-height: 1.6;
  color: var(--gray-blue);
}

a {
  text-decoration: none;
  color: white;
}

:root {
  --soft-blue: hsl(231, 69%, 60%);
  --soft-red: hsl(0, 94%, 66%);
  --gray-blue: hsl(229, 8%, 60%);
  --vd-blue: hsl(229, 31%, 21%);
  --overlay: hsla(229, 31%, 21%, 0.9);
}

:root {
  --font-rubik: "Rubik", sans-serif;
}